import "../styles/sections/ShortFilms.scss";
import Texture from '../assets/img/black-linen.png'
import Background from '../assets/img/desk.jpg'

function ShortFilms() {  
  return (
    <div className="section shortfilms"> 
      <div className="content" style={{zIndex: 2}}>
        <div className='title-block'>
          <h2 className='title'>MY WORK</h2>
        </div>
      </div>
      <div className="backdrop" style={{background: `url(${Texture}) repeat`, zIndex: 1}}></div>
      <div className="backdrop" style={{background: `url(${Background}) center bottom / 1200px no-repeat`, transform: 'scale(0.95px)', zIndex: 0, filter: 'blur(2px) grayscale(100%) contrast(20%) brightness(20%)', opacity: 1}}></div>
    </div>
  );
}

export default ShortFilms;
 
