import "../styles/components/Profile.scss"
//Images
import ProfileImage from '../assets/img/profile1.jpg';
//Icons
import WriteIcon from '../assets/svg/write.svg'
import ProdIcon from '../assets/svg/prod.svg'
import PreProdIcon from '../assets/svg/pre-prod.svg'
import EditIcon from '../assets/svg/edit.svg'

function Profile() {
  return (
    <div className="film-roll-container">
        <div className="above-profile rotate">
          <i className="write" style={{backgroundImage: `url(${WriteIcon})`}} />
          <i className="prod"  style={{backgroundImage: `url(${ProdIcon})`}} />
        </div>
        <div className="profile">
          <img src={ProfileImage} />
        </div>
        <div className="below-profile rotate-ninety">
          <i className="pre-prod" style={{backgroundImage: `url(${PreProdIcon})`}} />
          <i className="edit" style={{backgroundImage: `url(${EditIcon})`}} />
        </div>
      </div>
  );
}

export default Profile;  
