import { useRef } from "react"
import ReactTooltip from 'react-tooltip';
import Profile from '../components/Profile';
import '../styles/sections/IntroSection.scss';
import Background from '../assets/img/black-linen.png'
//Social Media
import Youtube from '../assets/svg/youtube.svg';
import Instagram from '../assets/svg/instagram.svg';
import Linkedin from '../assets/svg/linkedin.svg';
import Email from '../assets/svg/email.svg';

function IntroSection() {
  const mailRef = useRef(null);
  function handleMailClick(e){
    const tooltip = mailRef.current.tooltipRef;
    navigator.clipboard.writeText('mail.omkar.kulkarni@gmail.com')
    tooltip.classList.add('copied');
  }
  return (
    <div className="section intro" style={{background: `url(${Background}) repeat`}}> 
      <div className="introduction">
        <p className='pretitle'>Hi I'm  </p>
        <h1 className="title">Omkar<br/>Kulkarni</h1>
        <p className="subtitle">Writer / Director / Editor</p>
        <ul className="social-media">
          <li><a target="_blank" href="https://www.youtube.com/channel/UCfCxX5Vysv3_qIxz-sWr7uQ"><img src={Youtube}></img><div className='backdrop' style={{background: '#FF0000'}}></div></a></li>
          <li><a target="_blank" href="https://instagram.com/notreallyomkar"><img src={Instagram}></img><div className='backdrop' style={{background:'radial-gradient(circle at 33% 100%, #FED373 4%, #F15245 30%, #D92E7F 62%, #9B36B7 85%, #515ECF)'}}></div></a></li>
          <li><a target="_blank" href="https://linkedin.com/in/thinkingeek"><img src={Linkedin}></img><div className='backdrop' style={{background: '#2867B2'}}></div></a></li>
          <li><a onClick={handleMailClick} data-tip={"<div class='tooltip-body'><div class='mail-label'>mail.omkar.kulkarni@gmail.com</div><input id='addressRef' disabled='disabled' value='mail.omkar.kulkarni@gmail.com' /><div class='frontdrop'>COPIED!</div></div>"} data-html={true}>
            <img src={Email}></img>
            <div className='backdrop' style={{background: '#444'}}></div> 
          <ReactTooltip className='mail-tooltip' place='right' ref={mailRef} clickable={true} effect='solid' type="light"/>
          </a></li>
        </ul>
      </div>
      <Profile></Profile>
    </div>
  );
}

export default IntroSection;
 